import React from 'react';

import localStorageHelper from 'helpers/localStorageHelper';
import orderService from 'services/orderServices';

import './userOrdersSection.scss';

export default class UserOrdersSection extends React.PureComponent {
    constructor() {
        super();
        this.state = {
            orders: [],
            isLoading: true
        };
    }

    componentDidMount = async () => {
        const result = await orderService.setByUserId(localStorageHelper.getId());
        this.setState({
            orders: result,
            isLoading: false
        });
    }

    render() {
        if (this.state.isLoading) {
            return (
                <div className="add-book-page__preloader">
                    <Icon iconClassName="add-book-page__preloader-icon" icon={preloader} />
                </div>
            );
        }
        return (
            <section>
                <h1>My orders</h1>
                {}
            </section>
        );
    }
}
